<template>
    <div class="container">
      <div class="main__search">
          <input 
            type="text" 
            class="main__search-input"
            :placeholder="placeholder"
            v-model="value"
            autofocus
          >
          <div class="main__content-list" v-if="multiData">
            <SearchContent 
              v-for="item in multiData"
              :key="item.id"
              :item="item"
            />
          </div>
      </div>
    </div>
</template>

<script>
import SearchContent from '@/components/Search/SearchContent.vue'
import { mapState } from 'vuex'
export default {
  components: {
    SearchContent
  },
  props: ['placeholder'],
  data() {
    return {
      value: ''
    }
  },
  computed: {
    ...mapState('searching', ['searchData']),
    multiData() {
      if(this.value != '') {
        return  this.searchData
      }
    }
  },
  watch: {
    value(newVal) {
      this.$store.dispatch('searching/getSearchData', this.value)
    }
  },
  unmounted() {
    this.$store.commit('searching/resetSearch')
  }
}
</script>

